@font-face {
  font-family: 'Silkscreen Bold';
  src: url('./assets/fonts/Silkscreen-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Silkscreen Regular';
  src: url('./assets/fonts/Silkscreen-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Space Mono';
  src: url('./assets/fonts/SpaceMono-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Press Start 2P';
  src: url('./assets/fonts/PressStart2P-Regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.score{
  font-family: 'Silkscreen Regular', sans-serif;
}

/* MainMenu.css */
.menu-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  max-width: 100vw;
  color: white;
  font-family: 'Courier New', monospace;
  overflow: hidden;
  padding: 20px;
  box-sizing: border-box;
}

.menu-logo {
  font-family: 'Silkscreen Bold', 'Courier New', monospace;
  font-size: clamp(24px, 8vw, 40px);
  color: #FFF;
  margin-bottom: 50px;
  text-align: center;
  opacity: 0;
  transform: translateY(50px);
  word-break: break-word;
}

.logo-animate {
  animation: bounceInUp 0.8s forwards;
}

@keyframes bounceInUp {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  60% {
    opacity: 1;
    transform: translateY(-10px);
  }
  80% {
    transform: translateY(5px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.button-pressable {
  width: 100%;
  max-width: 420px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  border-radius: 10px;
  cursor: pointer;
  box-sizing: border-box;
}

.button {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  box-sizing: border-box;
}

.button-text {
  font-family: 'Silkscreen Bold', 'Courier New', monospace;
  font-size: clamp(18px, 4vw, 24px);
  color: black;
  text-align: center;
}

.button-flavor-text {
  font-family: 'Silkscreen Bold', 'Courier New', monospace;
  font-size: clamp(10px, 2.5vw, 14px);
  color: rgb(30, 30, 30);
  text-align: center;
  margin-top: 4px;
}

.menu-footer {
  font-family: 'Silkscreen Bold', 'Courier New', monospace;
  font-size: clamp(12px, 3vw, 16px);
  color: #555;
  position: absolute;
  bottom: 20px;
  opacity: 0;
  text-align: center;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.fade-in {
  animation: fadeIn 1s forwards;
  animation-delay: 0.5s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Game container styles */
#play-area {
  width: 100%;
  height: 100%;
  max-width: 100vw;
  max-height: 100vh;
  box-sizing: border-box;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .menu-container {
    padding: 10px;
  }
  
  .button-pressable {
    height: 50px;
    margin-bottom: 15px;
  }
  
  .menu-logo {
    margin-bottom: 30px;
  }
}